/* PHASE 1 */
.hide {
    display: none;
}

.todoName {
    cursor: pointer;
    color: #ddd; /* Light gray */
}

.todo {
    display: flex;
    flex-wrap: wrap;
}

.todo > * {
    flex-basis: 100%;
}

.deleteTodoButton,
.editButton {
    max-width: 30px;
}

* {
    font-size: 12px;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: #ddd; /* Light gray */
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #111; /* Dark background */
}

#main {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 90%;
    max-width: 600px; /* Adjust as needed */
    box-shadow: 5px -5px 0px rgba(255, 255, 255, 1); /* Adjusted shadow color */
    padding: 20px;
    background-color: #00000000; /* Darker background */
}

.project {
    border: 1px solid #474747; /* Lighter border */
    margin-bottom: 10px;
    padding: 10px;
    width: 100%;
    box-shadow: 1px 1px 2px rgba(255, 255, 255, 0.1); /* Adjusted shadow color */
}

.todo {
    border: 1px solid #303030; /* Lighter border */
    margin-bottom: 5px;
    padding: 5px;
    width: 100%;
}

table {
    width: 100%;
    border-collapse: collapse;
    font-family: monospace;
    color: #ddd; /* Light gray */
}

table, th, td {
    border: 1px solid #666; /* Lighter border */
}

th, td {
    padding: 5px 10px;
    text-align: left;
}

tr:nth-child(even) {
    background-color: #333; /* Darker background */
}

th {
    background-color: #555; /* Darker background */
    color: #ddd; /* Light gray */
}

.remove, 
.isReadTrue,
.isReadFalse {
    text-align: center;
}

button {
    border: 1px solid #303030; /* Lighter border */
}

button:hover {
    background-color: #444; /* Darker background */
}

.isReadFalse button {
    color: transparent;
}

.form-buttons {
    gap: 10px;
}

/* PHASE 2 */
/* Center dialogs */
dialog {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 1px solid #303030; /* Lighter border */
    background-color: #111; /* Darker background */
    padding: 20px;
    max-width: 400px; /* Adjust as needed */
    width: 90%;
    box-shadow: 2px -2px 0px 1px rgba(255,255,255,1);
}

/* Remove default dialog styling */
dialog::-webkit-backdrop {
    background-color: rgba(255, 255, 255, 0.3); /* Adjusted backdrop color */
}

/* PHASE 3 */
/* Center form elements vertically and horizontally */
dialog form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* Add some spacing between form elements */
dialog form label {
    margin-bottom: 5px;
}

/* Adjust input width */
dialog form input {
    width: 100%;
}

button {
    border: 1px solid #303030; /* Lighter border */
    color: #ddd; /* Light gray */
    background-color: #222; /* Darker background */
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
    padding-left: 5px;
    padding-right: 5px;
    padding-top: 2px;
    padding-bottom: 2px;
    margin-right: 5px;
}

button:hover {
    background-color: #444; /* Darker background */
    color: #fff; /* White */
}

.isReadFalse button {
    color: transparent;
}

/* Adjust input fields */
dialog form input[type="text"],
dialog form input[type="date"],
dialog form input[type="number"] {
    width: calc(100% - 20px); /* Adjusted width */
    border: 1px solid #666; /* Lighter border */
    background-color: #333; /* Dark background */
    color: #ddd; /* Light gray */
    padding-left: 5px;
    padding-right: 5px;
    padding-top: 2px;
    padding-bottom: 2px;
}

/* Rainbow hover effect */
/* .project:hover, */
.todo:hover,
.dialog:hover {
    animation: galacticNebula 20s linear infinite;
}


button:hover {
    animation: galacticNebula 1s linear infinite;
}

@keyframes galacticNebula {
    0% { background-color: #9932cc; } /* DarkOrchid */
    14% { background-color: #00ffff; } /* Cyan */
    28% { background-color: #ba55d3; } /* MediumOrchid */
    42% { background-color: #8a2be2; } /* BlueViolet */
    57% { background-color: #4169e1; } /* RoyalBlue */
    71% { background-color: #9932cc; } /* DarkOrchid */
    85% { background-color: #00ffff; } /* Cyan */
    100% { background-color: #ba55d3; } /* MediumOrchid */
}

@keyframes galacticNebula60 {
    0% { background-color: #9932cc60; } /* DarkOrchid */
    14% { background-color: #00ffff60; } /* Cyan */
    28% { background-color: #ba55d360; } /* MediumOrchid */
    42% { background-color: #8a2be260; } /* BlueViolet */
    57% { background-color: #4169e160; } /* RoyalBlue */
    71% { background-color: #9932cc60; } /* DarkOrchid */
    85% { background-color: #00ffff60; } /* Cyan */
    100% { background-color: #ba55d360; } /* MediumOrchid */
}`, "",{"version":3,"sources":["webpack://./src/styleDark.css"],"names":[],"mappings":"AAAA,2BAA2B;;AAE3B,YAAY;AACZ;IACI,aAAa;AACjB;;AAEA;IACI,eAAe;IACf,WAAW,EAAE,eAAe;AAChC;;AAEA;IACI,aAAa;IACb,eAAe;AACnB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;;IAEI,eAAe;AACnB;;AAEA;IACI,eAAe;IACf,SAAS;IACT,UAAU;IACV,sBAAsB;IACtB,WAAW,EAAE,eAAe;AAChC;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,iBAAiB;IACjB,sBAAsB,EAAE,oBAAoB;AAChD;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,UAAU;IACV,gBAAgB,EAAE,qBAAqB;IACvC,+CAA+C,EAAE,0BAA0B;IAC3E,aAAa;IACb,2BAA2B,EAAE,sBAAsB;AACvD;;AAEA;IACI,yBAAyB,EAAE,mBAAmB;IAC9C,mBAAmB;IACnB,aAAa;IACb,WAAW;IACX,gDAAgD,EAAE,0BAA0B;AAChF;;AAEA;IACI,yBAAyB,EAAE,mBAAmB;IAC9C,kBAAkB;IAClB,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,WAAW;IACX,yBAAyB;IACzB,sBAAsB;IACtB,WAAW,EAAE,eAAe;AAChC;;AAEA;IACI,sBAAsB,EAAE,mBAAmB;AAC/C;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,sBAAsB,EAAE,sBAAsB;AAClD;;AAEA;IACI,sBAAsB,EAAE,sBAAsB;IAC9C,WAAW,EAAE,eAAe;AAChC;;AAEA;;;IAGI,kBAAkB;AACtB;;AAEA;IACI,yBAAyB,EAAE,mBAAmB;AAClD;;AAEA;IACI,sBAAsB,EAAE,sBAAsB;AAClD;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,SAAS;AACb;;AAEA,YAAY;AACZ,mBAAmB;AACnB;IACI,eAAe;IACf,QAAQ;IACR,SAAS;IACT,gCAAgC;IAChC,yBAAyB,EAAE,mBAAmB;IAC9C,sBAAsB,EAAE,sBAAsB;IAC9C,aAAa;IACb,gBAAgB,EAAE,qBAAqB;IACvC,UAAU;IACV,gDAAgD;AACpD;;AAEA,kCAAkC;AAClC;IACI,0CAA0C,EAAE,4BAA4B;AAC5E;;AAEA,YAAY;AACZ,qDAAqD;AACrD;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA,2CAA2C;AAC3C;IACI,kBAAkB;AACtB;;AAEA,uBAAuB;AACvB;IACI,WAAW;AACf;;AAEA;IACI,yBAAyB,EAAE,mBAAmB;IAC9C,WAAW,EAAE,eAAe;IAC5B,sBAAsB,EAAE,sBAAsB;IAC9C,eAAe;IACf,6CAA6C;IAC7C,iBAAiB;IACjB,kBAAkB;IAClB,gBAAgB;IAChB,mBAAmB;IACnB,iBAAiB;AACrB;;AAEA;IACI,sBAAsB,EAAE,sBAAsB;IAC9C,WAAW,EAAE,UAAU;AAC3B;;AAEA;IACI,kBAAkB;AACtB;;AAEA,wBAAwB;AACxB;;;IAGI,wBAAwB,EAAE,mBAAmB;IAC7C,sBAAsB,EAAE,mBAAmB;IAC3C,sBAAsB,EAAE,oBAAoB;IAC5C,WAAW,EAAE,eAAe;IAC5B,iBAAiB;IACjB,kBAAkB;IAClB,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA,yBAAyB;AACzB,oBAAoB;AACpB;;IAEI,6CAA6C;AACjD;;;AAGA;IACI,4CAA4C;AAChD;;AAEA;IACI,KAAK,yBAAyB,EAAE,E